    <!-- 头部区域 -->
    <header class="header-section">
      <h1 class="city-title">天津旅行同伴</h1>
      <nav class="tab-nav">
        <button 
          :class="['tab-btn', { 'active-tab': activeTab === 'plans' }]" 
          @click="activeTab = 'plans'"
        >
          ️ 旅行计划
        </button>
        <button 
          :class="['tab-btn', { 'active-tab': activeTab === 'guides' }]" 
          @click="activeTab = 'guides'"
        >
           导游服务
        </button>
      </nav>
    </header>

    <!-- 内容区域 -->
    <main class="content-section">
      <!-- 旅行计划列表 -->
      <template v-if="activeTab === 'plans'">
        <div v-if="filteredPlans.length === 0" class="empty-state">
          <p class="empty-text">暂时没有找到旅行计划<br>点击下方按钮创建你的计划</p>
        </div>
        <div v-else class="card-grid">
          <article 
            v-for="plan in filteredPlans" 
            :key="plan.id" 
            class="plan-card"
            @click="startChat(plan)"
          >
            <div class="card-header">
              <h3>{{ plan.title }}</h3>
              <span class="date">{{ formatDate(plan.date) }}</span>
            </div>
            <p class="description">{{ plan.description }}</p>
            <div class="card-footer">
              <span class="destination">📍 {{ plan.destination }}</span>
              <span class="preference">🏷️ {{ plan.preference }}</span>
            </div>
            <div class="user-info">
              <img :src="plan.user.avatar || '/static/default-avatar.png'" class="avatar">
              <span>{{ plan.user.nickname }}</span>
            </div>
          </article>
        </div>
      </template>

      <!-- 导游服务列表 -->
      <template v-else>
        <div v-if="filteredGuides.length === 0" class="empty-state">
          <p class="empty-text">暂时没有可预约的导游<br>点击下方按钮发布你的服务</p>
        </div>
        <div v-else class="card-grid">
          <article 
            v-for="guide in filteredGuides" 
            :key="guide.id" 
            class="guide-card"
            @click="startChat(guide)"
          >
            <div class="card-header">
              <h3>{{ guide.title }}</h3>
              <span class="price">¥{{ guide.price }}</span>
            </div>
            <p class="description">{{ guide.description }}</p>
            <div class="card-footer">
              <span class="duration">⏳ {{ guide.duration }}小时</span>
              <span class="type">📌 {{ guide.type }}</span>
            </div>
            <div class="user-info">
              <img :src="guide.user.avatar || '/static/default-avatar.png'" class="avatar">
              <span>{{ guide.user.name }}</span>
            </div>
          </article>
        </div>
      </template>
    </main>

    <!-- 底部操作按钮 -->
    <footer class="action-footer">
      <button 
        v-if="activeTab === 'plans'" 
        @click="showPlanForm = true" 
        class="action-button primary-action"
      >
        ✏️ 发布旅行计划
      </button>
      <button 
        v-else 
        @click="showGuideForm = true" 
        class="action-button primary-action"
      >
        ✨ 发布导游服务
      </button>
    </footer>

    <!-- 发布旅行计划弹窗 -->
    <div v-if="showPlanForm" class="modal">
      <div class="modal-content">
        <h2>📝 发布旅行计划</h2>
        <form @submit.prevent="submitPlan">
          <div class="form-group">
            <input 
              type="text" 
              v-model="newPlan.title" 
              placeholder="请输入计划标题"
              class="form-input"
              required
            >
          </div>
          <div class="form-group">
            <picker mode="date" :value="newPlan.date" @change="onPlanDateChange">
              <view class="picker">{{ newPlan.date || '请选择日期' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <picker mode="selector" :range="tianjinPlaces" @change="onPlanDestinationChange">
              <view class="picker">{{ newPlan.destination || '请选择目的地' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <picker mode="selector" :range="preferenceOptions" @change="onPlanPreferenceChange">
              <view class="picker">{{ newPlan.preference || '请选择偏好' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <textarea
              v-model="newPlan.description"
              placeholder="请描述你的旅行计划（行程安排、注意事项等）"
              class="form-textarea"
              rows="4"
              required
            ></textarea>
          </div>
          <div class="form-actions">
            <button type="button" @click="showPlanForm = false" class="cancel-btn">取消</button>
            <button type="submit" class="submit-btn">立即发布</button>
          </div>
        </form>
      </div>
    </div>

    <!-- 发布导游服务弹窗 -->
    <div v-if="showGuideForm" class="modal">
      <div class="modal-content">
        <h2>✨ 发布导游服务</h2>
        <form @submit.prevent="submitGuide">
          <div class="form-group">
            <picker mode="selector" :range="tianjinPlaces" @change="onGuideTitleChange">
              <view class="picker">{{ newGuide.title || '请选择景点' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <picker mode="selector" :range="serviceTypeOptions" @change="onGuideTypeChange">
              <view class="picker">{{ newGuide.type || '请选择服务类型' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <picker mode="selector" :range="durationOptions" @change="onGuideDurationChange">
              <view class="picker">{{ newGuide.duration ? `${newGuide.duration}小时` : '请选择时长' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <picker mode="selector" :range="priceOptions" @change="onGuidePriceChange">
              <view class="picker">{{ newGuide.price ? `¥${newGuide.price}` : '请选择价格' }}</view>
            </picker>
          </div>
          <div class="form-group">
            <textarea
              v-model="newGuide.description"
              placeholder="请描述你的导游服务（包含内容、语言能力等）"
              class="form-textarea"
              rows="3"
              required
            ></textarea>
          </div>
          <div class="form-actions">
            <button type="button" @click="showGuideForm = false" class="cancel-btn">取消</button>
            <button type="submit" class="submit-btn">立即发布</button>
          </div>
        </form>
      </div>
    </div>

    <!-- 加载状态 -->
    <div v-if="isLoading" class="loading-overlay">
      <div class="loading-spinner"></div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import dayjs from 'dayjs'
import { http } from '@/api/http';

export default {
  setup() {
    // 状态管理
    const activeTab = ref('plans')
    const showPlanForm = ref(false)
    const showGuideForm = ref(false)
    const isLoading = ref(false)
    const plans = ref([])
    const guides = ref([])

    // 表单数据
    const newPlan = ref({
      title: '',
      date: '',
      destination: '',
      preference: '',
      description: ''
    })

    const newGuide = ref({
      title: '',
      type: '',
      duration: null,
      price: null,
      description: ''
    })

    // 静态数据
    const tianjinPlaces = [
      '五大道', '意式风情街', '天津之眼', '古文化街',
      '瓷房子', '海河游船', '天津博物馆', '盘山风景区'
    ]
    const preferenceOptions = ['美食', '历史', '自然', '摄影', '亲子']
    const serviceTypeOptions = ['历史讲解', '美食导览', '城市观光', '户外探险']
    const durationOptions = [2, 4, 6, 8]
    const priceOptions = ['100', '150', '200', '250', '300'];
	const onPlanDateChange = (e) => {
	  newPlan.value.date = e.detail.value; // 直接获取日期字符串
	};
    // 计算属性
    const filteredPlans = computed(() => {
      const today = dayjs().startOf('day')
      return plans.value.filter(plan => 
        dayjs(plan.date).isSameOrAfter(today)
      )
    })

    const filteredGuides = computed(() => {
      return guides.value.filter(guide => guide.city === '天津')
    })

    // 方法
    const formatDate = (date) => dayjs(date).format('YYYY-MM-DD')

    const fetchData = async (url) => {
      try {
        isLoading.value = true
        const response = await http.get(url)
        return response.data || []
      } catch (error) {
        console.error('数据获取失败:', error)
        return []
      } finally {
        isLoading.value = false
      }
    }

    const fetchPlans = async () => {
      const data = await fetchData('/travel-plans')
      plans.value = data.map(item => ({
        id: item.id,
        title: item.title,
        date: item.date,
        destination: item.destination,
        preference: item.preference,
        description: item.description,
        user: {
          id: item.user_id,
          nickname: item.nickname,
          avatar: item.avatar
        }
      }))
    }

    const fetchGuides = async () => {
      const data = await fetchData('/guide-services')
      guides.value = data.map(item => ({
        id: item.id,
        title: item.title,
        type: item.type,
        duration: item.duration,
        price: item.price,
        description: item.description,
        city: item.city,
        user: {
          id: item.user_id,
          name: item.name,
          avatar: item.avatar
        }
      }))
    }

    const submitForm = async (type, formData) => {
      try {
        isLoading.value = true
        const response = await http.post(type, formData)
        return response.data
      } catch (error) {
        console.error('提交失败:', error)
        throw error
      } finally {
        isLoading.value = false
      }
    }

    const submitPlan = async () => {
	  console.log('Submit data:', JSON.parse(JSON.stringify(newPlan.value)))
	  const form = newPlan.value
      if (!newPlan.value.title.trim()) {
        uni.showToast({ title: '请填写计划标题', icon: 'none' })
        return
      }
      if (!newPlan.value.date) {
        uni.showToast({ title: '请选择日期', icon: 'none' })
        return
      }
      if (!newPlan.value.destination) {
        uni.showToast({ title: '请选择目的地', icon: 'none' })
        return
      }
      if (!newPlan.value.preference) {
        uni.showToast({ title: '请选择旅行偏好', icon: 'none' })
        return
      }
      if (!newPlan.value.description.trim()) {
        uni.showToast({ title: '请填写计划描述', icon: 'none' })
        return
      }

      try {
        const createdPlan = await submitForm('/travel-plans', newPlan.value)
        plans.value.unshift({
          ...createdPlan,
          user: {
            id: createdPlan.user_id,
            nickname: createdPlan.nickname,
            avatar: createdPlan.avatar
          }
        })
        showPlanForm.value = false
        newPlan.value = { 
          title: '',
          date: '',
          destination: '',
          preference: '',
          description: ''
        }
        uni.showToast({ title: '发布成功', icon: 'success' })
      } catch (error) {
        uni.showToast({ 
          title: `发布失败: ${error.message || '未知错误'}`, 
          icon: 'none',
          duration: 3000 
        })
      }
    }

    const submitGuide = async () => {
	  console.log('Submit data:', JSON.parse(JSON.stringify(newPlan.value)))
      if (!newGuide.value.title) {
        uni.showToast({ title: '请选择景点', icon: 'none' })
        return
      }
      if (!newGuide.value.type) {
        uni.showToast({ title: '请选择服务类型', icon: 'none' })
        return
      }
      if (!newGuide.value.duration) {
        uni.showToast({ title: '请选择服务时长', icon: 'none' })
        return
      }
	  newGuide.value.price = Number(newGuide.value.price);
      if (typeof newGuide.value.price !== 'number') {
        uni.showToast({ title: '请选择价格', icon: 'none' })
        return
      }
	  if (newGuide.value.price <= 0) {
	    uni.showToast({ title: '价格必须大于0', icon: 'none' });
	    return;
	  }
	  if (typeof newGuide.value.price !== 'number' || isNaN(newGuide.value.price)) {
	    uni.showToast({ title: '价格无效', icon: 'none' });
	    return;
	  }

      try {
        const createdGuide = await submitForm('/guide-services', newGuide.value)
        guides.value.unshift({
          ...createdGuide,
          user: {
            id: createdGuide.user_id,
            name: createdGuide.name,
            avatar: createdGuide.avatar
          }
        })
        showGuideForm.value = false
        newGuide.value = {
          title: '',
          type: '',
          duration: null,
          price: null,
          description: ''
        }
        uni.showToast({ title: '发布成功', icon: 'success' })
      } catch (error) {
        uni.showToast({ 
          title: `发布失败: ${error.message || '未知错误'}`, 
          icon: 'none',
          duration: 3000 
        })
      }
    }

    const startChat = (item) => {
      uni.navigateTo({
        url: `/pages/chat/chat?userId=${item.user.id}&title=${encodeURIComponent(item.title)}`
      })
    }

    // Picker事件处理（已修复）
    const handlePickerChange = (e, options) => {
      const index = e.detail.value; // 获取选择的数组索引
      return options[index]; // 直接返回选项值
    };
    // 生命周期
    onMounted(() => {
      Promise.all([fetchPlans(), fetchGuides()])
    })

    return {
      activeTab,
      showPlanForm,
      showGuideForm,
      isLoading,
      plans,
      guides,
      newPlan,
      newGuide,
      tianjinPlaces,
      preferenceOptions,
      serviceTypeOptions,
      durationOptions,
      priceOptions,
      filteredPlans,
      filteredGuides,
      formatDate,
      submitPlan,
      submitGuide,
      startChat,
	  onPlanDateChange,
      onPlanDestinationChange: (e) => newPlan.value.destination = handlePickerChange(e, tianjinPlaces),
	  onPlanPreferenceChange: (e) => newPlan.value.preference = handlePickerChange(e, preferenceOptions),
      onGuideTitleChange: (e) => newGuide.value.title = handlePickerChange(e, tianjinPlaces),
      onGuideTypeChange: (e) => newGuide.value.type = handlePickerChange(e, serviceTypeOptions),
      onGuideDurationChange: (e) => newGuide.value.duration = handlePickerChange(e, durationOptions),
      onGuidePriceChange: (e) => {
        const value = handlePickerChange(e, priceOptions)
        newGuide.value.price = Number(value)
      }
    }
  }
}
</script>

